.profile__setting-top {
  padding: 25px;
  border-bottom: 1px solid #e2ebfa;
}
.profile__setting {
  background: #ffffff;
  border: 1px solid #e2ebfa;
  border-radius: 6px;
}
.setting__btn {
  line-height: normal;
  letter-spacing: normal;
  padding: 10px;
  max-height: 40px;
  color: #000000;
  border-radius: 4px;
  font-weight: 400;
  text-transform: capitalize;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
}
.cancel__btn {
  background: #f1f5f5;
  border: 1px solid #f1f5f5;
}
.save__btn {
  background-color: #6c5878;
  border: 1px solid #6c5878;
  color: #ffffff;
}
.profile__setting-title h3 {
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  color: #000000;
}
.profile__setting-title p {
  font-size: 16px;
  line-height: 24px;
  color: #868e96;
  margin-top: 10px;
}
.profile__setting-form {
  padding: 25px 0px;
  max-width: 700px;
  margin: auto;
}
.profile__setting-form .signup__form-one h4 {
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  color: #6c5878;
  margin: 30px 0 20px;
}
.profile__setting-form .profile__picture {
  border-top: 0;
  padding-top: 0;
  margin-top: 0;
}
.profile__setting .signup__form-one input::placeholder {
  color: #212529;
  font-weight: 400;
  font-size: 14px;
}

.disabled__input {
  background-color: #f1f5f5 !important;
}

@media only screen and (max-width: 1280px) {
  .profile__setting-form .signup__form-one h4 {
    margin: 10px 0;
  }
}
@media only screen and (max-width: 550px) {
  .profile__setting-top {
    padding: 20px 10px;
  }
  .profile__setting-form {
    padding: 20px 10px;
  }
  .profile__setting-title p {
    margin: 15px 0;
  }
}
