.container {
  max-width: 535px;
  width: 100%;
  height: auto;
  border-radius: 6px;
  border: 1px solid #ffffff;
  height: auto;
  background-color: white;
  display: flex;
  flex-direction: column;
  padding: 20px;
  z-index: 999999;
}

.crossContainer {
  height: 30px;
  width: 30px;
  border-radius: 8px;
  border: 1px solid #dce5ef;
  display: flex;
  align-items: center;
  justify-content: center;
  &:hover {
    cursor: pointer;
  }
  align-self: flex-end;
}

.crossIcon {
  height: 20px;
  width: 20px;
  path {
    stroke: #001b4c;
  }
}

.title {
  font-family: Poppins;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.desc {
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: #747a80;
}

.inputLabel {
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
  color: #000000 !important;
}

.inputStyle {
  height: 64px;
  width: 100%;
  border-radius: 8px;
  border: 1px solid #cfdbd5;
  background-color: transparent;
  padding: 8px;
}

.btn {
  height: 44px;
  width: 47%;
  border-radius: 8px;
  background-color: #6c5878;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  color: white !important;
}

.cancel {
  background-color: #f2f4f7 !important;
  color: #001b4c !important;
}
