.searchContainer {
  height: 44px;
  width: 225px;
  border: 1px solid #d0d5dd;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 10px;
  padding-right: 10px;
  gap: 6px;
}

.Icon {
  min-height: 20px;
  max-height: 20px;
  min-width: 20px;
  max-width: 20px;
  height: 100%;
  width: 100%;
  path {
    fill: #667085;
  }
}

.inputStyle {
  height: 100%;
  border: 0px;
  width: 100%;
  outline: 0px;
  &:focus {
    border: 0px !important;
  }
}

.crossIcon {
  min-height: 20px;
  max-height: 20px;
  min-width: 20px;
  max-width: 20px;
  height: 100%;
  width: 100%;
  path {
    stroke: #667085;
  }
  &:hover {
    cursor: pointer;
  }
}
