@import "shared/utils/colors.scss";
.dahboard__header {
  padding: 12px 30px;
  box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.1);
  position: relative;
}
.header__page-title {
  letter-spacing: 0.03em;
  font-weight: 700;
  font-size: 12px;
  line-height: 18px;
  color: $TRYIAN_BLUE;
  text-transform: uppercase;
}
.header__notification-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 40px;
  border: 1px solid #ffc4b4;
  border-radius: 6px;
  background-color: transparent;
  position: relative;
}

.header__notification-btn:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1);
}
.new_notification::after {
  position: absolute;
  top: 10px;
  right: 12px;
  content: "";
  width: 4.9px;
  height: 4.9px;
  border-radius: 50%;
  background-color: #ffc4b4;
}

.header__user-box {
  margin-left: 15px;
  position: relative;
  cursor: pointer;
}
.user__dropdown {
  position: absolute;
  right: 0;
  width: 250px;
  top: 100%;
  background: #fff;
  display: none;
  padding: 24px 20px;
  border-radius: 10px;
  box-shadow: 1px 1px 28px rgba(0, 0, 0, 0.1);
  margin-top: 12px;
  z-index: 999;
}

.user__dropdown.active {
  display: block;
}

.user__dropdown button,
.user__dropdown a {
  width: 100%;
  border: none;
  background: none;
  display: block;
  padding: 7px 0;
  text-align: left;
  font-size: 14px;
  text-transform: capitalize;
  font-weight: 400;
  color: #33475b;
}
.user__dropdown button svg,
.user__dropdown a svg {
  width: 22px;
  height: 22px;
  margin-right: 14px;
  vertical-align: middle;
  display: inline-block;
}
.mobile--box-info {
  display: none;
}
.user__box-img {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  border-radius: 100%;
  background: #6c5878;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  letter-spacing: 2px;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 500;
}
.user__box-info {
  cursor: pointer;
}
.user__box-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.user__name {
  color: $TRYIAN_BLUE;
  font-weight: 400;
}
.user__name svg {
  margin-left: 10px;
}
.user__email {
  color: #959cb6;
  font-size: 12px;
  line-height: 18px;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 21ch;
}
.notification-count {
  position: absolute;
  top: 1px;
  border-radius: 50%;
  font-size: 10px;
  font-weight: 800;
  border: 1px solid #495057;
  background-color: #ffc4b4;
  right: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 18px;
  width: 18px;
  color: black;
}
/**/

.notification-card {
  background: #ffffff;
  box-shadow: 0px 20px 24px -4px rgba(16, 24, 40, 0.1),
    0px 8px 8px -4px rgba(16, 24, 40, 0.04);
  border-radius: 8px;
  padding: 24px 24px 10px;
  position: absolute;
  width: 100%;
  min-width: 450px;
  right: -150px;
  top: 20px;
  display: none;
  z-index: 999;
  max-height: 400px;
  overflow-y: auto;
}
.notification.show-notificaiton-bar .notification-card {
  display: block;
}
.notification-list {
  position: relative;
  isolation: isolate;
}
.notification-list:has(.notification-bar)::after {
  position: absolute;
  top: 0;
  left: 27px;
  width: 2px;
  height: 100%;
  content: "";
  z-index: -1;
  background: #e4e7ec;
  border-radius: 2px;
}
.notification {
  position: relative;
}
.user-notification-img {
  width: 56px;
  border-radius: 50%;
}
.notification-message {
  width: 100%;
  max-width: calc(100% - 68px);
}
.notification-header {
  margin-bottom: 24px;
}
.notification-header h6 {
  font-weight: 500;
  font-size: 20px;
  color: #101828;
}
.notification-header h6 span {
  font-weight: 500;
  font-size: 12px;
  color: #323c45;
  background: #fff5f1;
  padding: 2px;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  display: inline-block;
  text-align: center;
}
.notification-header a,
.mark-read-btn {
  font-weight: 500;
  font-size: 14px;
  color: #6c5878;
  text-transform: none;
}
.notification-bar:not(:last-child) {
  margin-bottom: 24px;
}
.notification-message span {
  font-weight: 400;
  font-size: 12px;
  display: block;
  color: #667085;
}
.notification-message {
  margin-left: 12px;
}
.notification-message p {
  font-weight: 500;
  font-size: 13px;
}
.notification-message p strong {
  margin-right: 5px;
  color: #344054;
}
.user-notification-img img,
.user-notification-img svg {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  margin: 0 auto;
  display: block;
  object-fit: cover;
}
.no-result {
  font-size: 16px;
  text-align: center;
  display: block;
  font-weight: 600;
}
@media only screen and (max-width: 1280px) {
  .dahboard__header {
    padding: 12px 20px;
  }
}
@media only screen and (max-width: 767px) {
  .user__email {
    max-width: 35ch;
  }
}
@media only screen and (max-width: 550px) {
  .user__box-info {
    display: none;
  }
  .mobile--box-info {
    display: block;
  }
  .user__dropdown {
    width: 300px;
    right: 0;
    left: inherit;
  }
  .user__box-img {
    margin: 0;
  }
  .notification-card {
    min-width: calc(100vw - 20px);
    right: -55px;
  }
  .dahboard__header {
    padding: 12px 10px;
  }
}
