@import "shared/utils/colors.scss";

.paginationContainer {
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  padding: 10px;
  .paginationItem {
    height: 38px;
    width: 38px;
    text-align: center;
    margin: auto 4px;
    color: $GRAY_500;
    display: flex;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.01071em;
    border-radius: 8px;
    line-height: 1.43;
    font-size: 14px;
    // font-family: InterMedium;
    min-width: 32px;

    &:hover {
      background: #fff5f1;
      color: $BLACK !important;
      cursor: pointer;
    }
  }
}

.active {
  background: #fff5f1;
  color: $BLACK !important;
}

.dots {
  color: $GRAY_500;
}

.arrowContainer {
  height: 38px;
  // width: 38px;
  border: 1px solid #eae9f2;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
}

.actionLabel {
  // font-family: InterMedium;
  font-size: 14px;
  color: $BLACK;
}
.iconStyle path {
  fill: black;
}

.smallScreenDisplay {
  display: none;
}

@media screen and (max-width: 700px) {
  .display {
    display: none;
  }
  .paginationContainer {
    flex-direction: column-reverse;
    align-items: center;
  }
  .smallScreenDisplay {
    display: flex;
  }
}
