.accordContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
  border-left: 1px solid #e4e4e7;
  border-right: 1px solid #e4e4e7;
  z-index: 99;
  position: relative;
  border-radius: 10px;
}

.singleAccord {
  min-height: 90px;
  height: 100%;
  width: 100%;
  border-top: 1px solid #e4e4e7;
  border-bottom: 1px solid #e4e4e7;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px;
  transition: all 0.3s ease-in-out;

  .content {
    display: flex;
    align-items: start;
    justify-content: start;
    flex-direction: column;
    label {
      font-family: Poppins;
      font-size: 21px;
      font-style: normal;
      font-weight: 600;
      line-height: 30px;
      color: black;
    }
    span {
      font-family: Poppins;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 30px;
      color: black;
    }
  }

  .minusIcon {
    min-width: 16.938px;
    max-width: 16.938px;
    min-height: 2.143px;
    max-height: 2.143px;
    height: 100%;
    width: 100%;
  }

  .plusIcon {
    min-width: 16.938px;
    max-width: 16.938px;
    min-height: 15px;
    max-height: 15px;
    height: 100%;
    width: 100%;
  }

  &:hover {
    cursor: pointer;
    .content {
      label {
        cursor: pointer;
      }
    }
  }
}

.borderTop {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.borderBottom {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
