@import "shared/utils/colors.scss";
.cards__box {
  margin-bottom: 25px;
}
.main__card {
  box-shadow: 0px 1px 3px rgba(16, 24, 40, 0.1),
    0px 1px 2px rgba(16, 24, 40, 0.06);
  border-radius: 8px;
  background-color: $WHITE;
  padding: 20px 15px;
}
.main__card-top {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.main__card-top p {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #101828;
  margin-left: 20px;
}
.main__card-number {
  font-weight: 600;
  font-size: 30px;
  line-height: 38px;
  color: #101828;
  min-height: 38px;
}
.student__cards-box .main__card {
  /*width: calc(50% - 20px);*/
  flex: 1;
}
.elder__cards-box .main__card {
  width: calc(33.33% - 20px);
}
.institue__cards-box .main__card {
  width: calc(25% - 20px);
}

@media only screen and (max-width: 1024px) {
  .main__body {
    width: calc(100% - 80px) !important;
  }
}

@media only screen and (max-width: 880px) {
  .student__cards-box .main__card {
    flex: 1 100%;
  }
}

@media only screen and (max-width: 767px) {
  .main__body {
    width: calc(100% - 50px) !important;
  }
}
