@import "shared/utils/colors.scss";

.optionsContainer {
  position: absolute;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  width: 212px;
  background-color: white;
  filter: drop-shadow(0px 0px 8px rgba(0, 0, 0, 0.1));
  right: 0px;
  top: 45px;
  z-index: 9999999999;
}

.labelStyle {
  font-size: 14px;
  font-family: Poppins;
  font-weight: 400;
  color: #33475b;
  &:hover {
    cursor: pointer;
  }
}

.iconStyle {
  height: 20px;
  width: 20px;
}

.optionContainer {
  // border-bottom: 1px solid $GRAY_500;
  &:hover,
  &:hover > .labelStyle,
  &:hover > .iconStyle > path {
    background-color: #f2f2f2;
    color: #33475b;
  }
}

.topradius {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.btmradius {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.color2 path {
  stroke: #ea4a33 !important;
}

.labelStyle2 {
  color: #ea4a33 !important;
}
