.landingFooter {
  padding: 40px 0;
}
.footerLevelOne {
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(18, 20, 29, 0.1);
}
.footerMenuList a {
  text-transform: uppercase;
  color: #12141d;
}
.footerMenuList a {
  text-transform: uppercase;
  color: #12141d;
}
.footerLevelTwo p {
  font-size: 16px;
  line-height: 26px;
  color: #12141d;
}
.privacyMenu a {
  font-size: 16px;
  line-height: 26px;
  color: #12141d;
}

@media only screen and (max-width: 767px) {
  .landingFooter {
    padding: 40px 0 10px;
  }
  .footerLevelOne {
    flex-direction: column;
  }
  .footerMenuList {
    margin: 20px 0;
  }
  .footerLevelTwo {
    flex-direction: column;
  }
  .privacyMenu {
    margin: 5px 0;
  }
}
