.container {
  max-width: 1240px;
  margin: auto;
  padding: 20px 0px;
  div {
    word-break: break-all;
  }
}

.heading {
  color: #495057;
  font-family: Poppins;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 40px;
}

@media screen and (max-width: 1240px) {
  .container {
    padding: 20px 20px;
  }
}
