.ourWork {
  padding: 100px 0;
}
.workInfo {
  width: calc(50% - 50px);
}
.workImg {
  width: calc(50% - 50px);
}
.workInfo h2 {
  font-weight: 500;
  font-size: 48px;
  line-height: 1.3;
  color: #183b56;
}
.workTabbing {
  margin-top: 50px;
}
.workTab {
  padding: 20px;
  position: relative;
  border-radius: 0px 8px 8px 0px;
  cursor: pointer;
  transition: 0.2s all linear;
  margin: 20px 0px;
}
.workTab.active,
.workTab:hover {
  background-color: rgba(179, 186, 197, 0.1);
}
li.workTab .tabscreenOne {
  display: none;
}
.workTab.active:before,
.workTab:hover:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  height: 90%;
  width: 2px;
  background-color: #5a7184;
  margin: auto;
}
.workTab h4 {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0.2px;
  color: #183b56;
}
.workTab p {
  font-size: 16px;
  line-height: 1.8;
  color: #5a7184;
  margin-top: 10px;
}
.workImgTabscreen {
  box-shadow: 0px 25px 35px rgba(0, 0, 0, 0.03);
  border-radius: 20px;
  padding: 45px;
  position: relative;
}
.workImgTabscreen:before {
  content: "";
  position: absolute;
  background-image: url(../../../../assets/icons/camera.svg);
  background-size: contain;
  background-repeat: no-repeat;
  width: 11px;
  height: 11px;
  left: 0;
  right: 0;
  margin: auto;
  top: 14px;
}
.workImgTabscreen img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.textDecoration {
  position: relative;
  z-index: 2;
}
.textDecoration:before {
  content: "";
  position: absolute;
  bottom: 10px;
  width: 100%;
  height: 12px;
  background: #ffc4b4;
  z-index: -1;
}
.tabscreenOne {
  display: none;
}
.tabscreenOne.active {
  display: block;
}

@media only screen and (max-width: 1280px) {
  .workImg {
    width: calc(50% - 10px);
  }
  .workInfo {
    width: calc(50% - 10px);
  }
}

@media only screen and (max-width: 1024px) {
  .ourWork {
    padding: 50px 0;
  }
  .workInfo h2 {
    font-size: 42px;
  }
  .workTab h4 {
    font-size: 22px;
  }
}

@media only screen and (max-width: 767px) {
  .ourWork {
    padding: 50px 0;
  }
  .workInfo h2 {
    font-size: 38px;
  }
  .workInfo {
    width: calc(100% - 0px);
  }
  .workTab.active,
  .workTab:hover {
    background-color: transparent;
  }
  .workTabbing {
    margin-top: 0px;
  }
  .workTab.active h4,
  .workTab:hover h4,
  .workTab.active p,
  .workTab:hover p {
    background-color: rgba(179, 186, 197, 0.1);
  }
  .workTab p {
    margin: 0;
  }
  .workTab p,
  .workTab h4 {
    padding: 5px 15px;
    border-radius: 10px;
  }
  .workTab .tabscreenOne {
    margin-top: 50px;
  }
  .workTab p {
    border-top-right-radius: 0px;
    border-top-left-radius: 0px;
  }
  .workTab h4 {
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
  }
  li.workTab.active .tabscreenOne.active {
    display: block;
  }
  .tabscreenOne img {
    margin: 0 auto;
    display: block;
    width: 100%;
  }
  .workImg {
    display: none;
  }
}

@media only screen and (max-width: 550px) {
  .workInfo h2 {
    font-size: 32px;
  }
}
