.container {
  height: 46px;
  width: 100%;
  border: 1px solid #cfdbd5;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 6px;
  border-radius: 8px;
  &:hover {
    cursor: pointer;
  }
}

.placeholderStyle {
  color: #868e96;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

.iconStyle {
  height: 24px;
  width: 24px;
}

.valueLabel {
  color: black;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  text-transform: capitalize;
}

.optionsContainer {
  position: absolute;
  width: 100%;
  max-height: 400px;
  overflow-y: auto;
  min-height: 200px;
  border-radius: 8px;
  background-color: white;
  filter: drop-shadow(0px 0px 8px rgba(0, 0, 0, 0.1));
  z-index: 401;
  top: 5px;
  left: 0px;
}

.itemContainer {
  height: 72px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 20px;
  padding-right: 20px;
  gap: 10px;
  &:hover {
    cursor: pointer;
  }
  img {
    height: 32px;
    width: 32px;
    border-radius: 100%;
    object-fit: cover;
  }
  .contentContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 2px;
    label {
      font-family: Poppins;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      color: black;
      &:hover {
        cursor: pointer;
      }
      text-transform: capitalize;
    }
    span {
      font-family: Inter;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 20px;
      color: #6c5878;
    }
  }

  &:hover {
    background-color: #e4e7ec;
  }
}

.borderBtm {
  border-bottom: 1px solid #e4e7ec;
}

.backDropContainer {
  height: 100px;
  min-width: 500px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  z-index: 400;
  padding: 0px !important;
  transition: visibility 0.2s;
  overflow: hidden;
  top: -60px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  right: 0px;
}

.notFoundContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  label {
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    color: black;
  }
}
