.testimonial__section .swiper-pagination {
  position: absolute;
  top: 60% !important;
}
.testimonial__section .swiper-pagination .swiper-pagination-bullet-active {
  width: 6px;
}

.swiper-pagination-bullet {
  background: #6c5878;
  height: 6px;
  opacity: 0.54;
  width: 6px;
}

.swiper-pagination-bullet-active {
  border-radius: 4px;
  opacity: 1;
  width: 18px;
}

@media only screen and (max-width: 1024px) {
  .testimonial__section .swiper-pagination {
    position: absolute;
    bottom: 0% !important;
    top: inherit !important;
  }
}

@media only screen and (max-width: 767px) {
  .testimonial__section .swiper-pagination {
    position: absolute;
    bottom: 0% !important;
    top: inherit !important;
  }
}
