@import "shared/utils/colors.scss";

.optionsContainer {
  position: absolute;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: white;
  filter: drop-shadow(0px 0px 8px rgba(0, 0, 0, 0.1));
  z-index: 401;
}

.labelStyle {
  font-size: 14px;
  font-family: Poppins;
  font-weight: 400;
  color: #33475b;
  &:hover {
    cursor: pointer;
  }
}

.iconStyle {
  height: 16px;
  width: 16px;
}

.optionContainer {
  &:hover,
  &:hover > .labelStyle,
  &:hover > .iconStyle > path {
    background-color: #f2f2f2;
    color: #33475b;
    fill: #33475b;
    cursor: pointer;
  }
}

.topradius {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.btmradius {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.active {
  background-color: #f2f2f2;
  color: #33475b;
  fill: #33475b;
}

.backDropContainer {
  height: 100vh;
  min-width: 500px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  z-index: 400;
  padding: 0px !important;
  transition: visibility 0.2s;
  overflow: hidden;
  bottom: -150px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  right: 0px;
}
