@import "shared/utils/colors.scss";

.editBtn {
  background-color: transparent !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  align-self: center !important;
  margin-bottom: 20px !important;
  font-size: 14px !important;
  font-weight: 400 !important;
  text-decoration: underline !important;
  color: #031e4d;
}

.tabsContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.tab {
  text-align: center;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: $GRAY_500;
  width: 50%;
  border-bottom: 1px solid #e2ebfa;
  padding-bottom: 20px;
  &:hover {
    cursor: pointer;
    font-style: normal;
    font-weight: 600 !important;
    border-bottom: 3px solid $GRAY_500 !important;
  }
  transition: all 0.2s ease-in-out;
}

.active {
  font-style: normal;
  font-weight: 600 !important;
  border-bottom: 3px solid $GRAY_500 !important;
}

.detailContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
}

.detail {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.icon {
  height: 24px;
  width: 24px;
  path {
    fill: #868e96;
  }
}

.label {
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: $GRAY_500;
}

.seperator {
  width: 100%;
  height: 1px;
  background-color: #e2ebfa;
}

.title {
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  color: $GRAY_500;
}

.desc {
  font-family: Poppins;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  color: $ROLLING_STONE;
}
