.swiper-container {
  height: 100%;
  max-height: 100vw;
  min-height: 0;
  min-width: 0;
  max-width: 100vw;
  width: 100%;
  overflow: hidden;
}
.swiper-slide {
  width: auto;
  flex-shrink: 0;
  display: block;
  height: 100%;
  max-height: 100%;
}
.swiper-wrapper {
  max-height: 100%;
  width: 100% !important;
  height: 100%;
  display: flex;
}
.swiper-pagination-bullet {
  background: #6c5878;
  opacity: 0.54;
  height: 6px;
  width: 6px;
}
.swiper-pagination-bullet-active {
  opacity: 1;
  width: 18px;
  border-radius: 4px;
}
