.ctaBtn {
  border: 1px solid #6c5878;
  box-shadow: 0px 1px 2px rgb(16 24 40 / 5%);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  background: #6c5878;
  color: #f8f9fa;
  padding: 10px 20px;
  transition: 0.2s all linear;
  text-transform: capitalize;
}
.ctaBtn svg {
  margin-left: 10px;
  transition: 0.2s all linear;
}
.ctaBtn:hover {
  background-color: transparent;
  color: #6c5878;
}
.ctaBtn:hover svg path {
  fill: #6c5878;
}
.heroBanner {
  background-image: url(../../../assets/images/landing-heroBanner-background.png);
  background-size: 100% 86%;
  background-position: top;
  padding-top: 90px;
  background-repeat: no-repeat;
}
.heroTitle {
  max-width: 1200px;
  text-align: center;
  margin: 0px auto 40px;
  position: relative;
}

.heroImg {
  position: relative;
}

.heroTitle h1 {
  font-weight: 600;
  font-size: 46px;
  line-height: 1.3;
  letter-spacing: 0.2px;
  color: #000000;
}
.heroTitle p {
  font-size: 20px;
  line-height: 1.6;
  color: #495057;
  max-width: 840px;
  margin: 20px auto;
}
.heroImg img {
  width: 100%;
}
.textDecoration {
  position: relative;
  z-index: 2;
}
.textDecoration:before {
  content: "";
  position: absolute;
  bottom: 10px;
  width: 100%;
  height: 12px;
  background: #ffc4b4;
  z-index: -1;
}
.everyoneLove {
  padding: 25px 0;
  margin: 25px 0;
}
.everyoneContent {
  padding-bottom: 40px;
  border-bottom: 1px solid #e5eaf4;
}
.everyoneContent > p {
  max-width: 780px;
  font-size: 18px;
  line-height: 32px;
  color: #5a7184;
}
.aboutImg {
  width: calc(50% - 50px);
}
.aboutImg img {
  width: 100%;
}
.aboutInfo {
  width: calc(50% - 50px);
}
.aboutInfo h1 {
  font-weight: 500;
  font-size: 48px;
  line-height: 1.3;
  color: #183b56;
}
.aboutInfo p {
  font-size: 18px;
  line-height: 1.7;
  color: #5a7184;
  padding: 25px 0 50px;
}
.aboutUs {
  padding-bottom: 100px;
}
.featuredBoxItem {
  width: calc(33.33% - 30px);
  background-color: #fff;
  padding: 25px 35px 45px;
  border: 1px solid #f1f1f1;
  border-radius: 8px;
}
.ourFeatured {
  background: rgba(255, 243, 239, 0.44);
  padding: 100px 0;
}
.featuredBoxItem img {
  width: 80px;
  height: 80px;
  object-fit: contain;
}
.featuredBoxContent h3 {
  font-size: 24px;
  line-height: 30px;
  color: #494949;
  font-weight: 600;
}
.featuredBoxContent p {
  font-size: 15px;
  line-height: 1.8;
  color: #5a7184;
  margin: 15px 0;
}

.aboutUs {
  padding-bottom: 100px;
}

.connectGeneration {
  padding-bottom: 100px;
}
.generationImg {
  width: calc(50% - 20px);
}
.generationImg img {
  width: 100%;
}
.generationInfo {
  width: calc(50% - 20px);
}
.generationInfo h2 {
  font-weight: 500;
  font-size: 42px;
  line-height: 1.4;
  color: #183b56;
}
.generationInfo p {
  font-size: 18px;
  line-height: 32px;
  color: #5a7184;
  margin: 25px 0 50px;
}
.generationList li {
  position: relative;
  width: calc(50% - 10px);
  padding-left: 35px;
  font-size: 16px;
  line-height: 32px;
  color: #323c45;
}
.generationList li:before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  background-image: url(../../../assets/icons/tickWithCircle.svg);
  background-size: contain;
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
}
.contactSection {
  background-image: url(../../../assets/images/contact-banner.png);
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
}
.contactContent {
  max-width: 910px;
  text-align: center;
  margin: 0 auto;
  padding: 100px 0;
}
.contactContent h2 {
  font-weight: 600;
  font-size: 32px;
  line-height: 1.2;
  color: #fff;
}
.ctaBtn.whiteCta {
  margin: 28px auto 0;
  background-color: #fff;
  color: #2f5d5e;
  border: 1px solid white;
  display: inline-block;
  vertical-align: top;
}
.ctaBtn.inline {
  display: inline-block;
  vertical-align: middle;
  padding: 9px 20px;
}
.whiteCta:hover {
  color: white;
  background-color: transparent;
}

.faqSection {
  padding: 100px 0;
  position: relative;
}
.faqSection::after {
  position: absolute;
  right: 100px;
  width: 291.1px;
  height: 291.1px;
  bottom: 100px;
  content: "";
  background: #ffc4b4;
  opacity: 0.56;
  filter: blur(162.323px);
}
.faqContent h2 {
  font-size: 48px;
  line-height: 60px;
  font-weight: 500;
  color: #183b56;
  margin-bottom: 50px;
  text-align: center;
}

@media only screen and (max-width: 1440px) {
  .heroImg:before {
    left: 0;
  }
  .heroImg:after {
    right: 0;
  }
}
@media only screen and (max-width: 1280px) {
  .heroTitle:before,
  .heroTitle:after,
  .heroImg:after,
  .heroImg:before {
    content: none;
  }
  .aboutInfo {
    width: calc(50% - 15px);
  }
  .aboutImg {
    width: calc(50% - 15px);
  }

  .featuredBoxItem {
    width: calc(33.33% - 10px);
    padding: 20px;
  }

  .innerLeftImg,
  .innerRightImg {
    display: none;
  }

  .aboutUs {
    padding-bottom: 50px;
  }
  .contactContent {
    padding: 50px 0;
  }
  .ourFeatured {
    padding: 50px 0;
  }
  .innerHolder {
    padding: 40px 0;
  }
  .faqSection {
    padding: 50px 0;
  }
  .connectGeneration {
    padding-bottom: 50px;
  }
}

@media only screen and (max-width: 1024px) {
  .heroTitle h1 {
    font-size: 40px;
  }
  .aboutInfo h1 {
    font-size: 42px;
  }

  .featuredBoxContent h3 {
    font-size: 22px;
  }

  .generationInfo h2 {
    font-size: 36px;
  }
  .faqContent h2 {
    font-size: 42px;
  }
  .innerHolder h3,
  .testimonialSectionMobile h3 {
    font-size: 36px;
  }
  .contactContent h2 {
    font-size: 28px;
  }
  .heroTitle:after {
    right: -7%;
  }
  .everyoneContent > p {
    max-width: 80%;
  }
  .heroBanner {
    padding-top: 30px;
  }
}

@media only screen and (max-width: 767px) {
  .heroTitle h1 {
    font-size: 36px;
  }
  .aboutInfo h1 {
    font-size: 38px;
  }

  .generationInfo h2 {
    font-size: 32px;
  }
  .generationInfo p {
    margin: 15px 0;
  }
  .innerHolder h3,
  .testimonialSectionMobile h3 {
    font-size: 32px;
  }
  .faqContent h2 {
    font-size: 38px;
    margin-bottom: 20px;
  }

  .contactContent h2 {
    font-size: 24px;
  }
  .everyoneContent {
    flex-direction: column-reverse;
  }
  .everyoneContent > p {
    max-width: 100%;
  }
  .heroTitle p {
    margin: 10px auto 0;
  }
  .heroTitle {
    margin: 0px auto 20px;
  }
  .aboutInfo {
    width: calc(100% - 0px);
  }
  .aboutInfo p {
    padding: 10px 0 10px;
  }
  .aboutImg {
    width: calc(100% - 0px);
  }
  .featuredBox {
    gap: 10px 0;
  }
  .featuredBoxItem {
    width: calc(100% - 0px);
  }

  .generationImg {
    width: calc(100% - 0px);
  }
  .generationInfo {
    width: calc(100% - 0px);
  }
}

@media only screen and (max-width: 550px) {
  .heroTitle h1 {
    font-size: 30px;
  }
  .everyoneContent > p {
    font-size: 16px;
  }
  .aboutInfo h1 {
    font-size: 32px;
  }
  .aboutInfo p {
    font-size: 16px;
  }

  .generationInfo h2 {
    font-size: 28px;
  }
  .innerHolder h3,
  .testimonialSectionMobile h3 {
    font-size: 28px;
  }

  .generationList li {
    width: calc(100% - 0px);
  }
  .faqContent h2 {
    font-size: 32px;
  }

  .contactContent h2 {
    font-size: 20px;
  }
}
