.container {
  max-width: 420px;
  width: 100%;
  height: auto;
  border-radius: 8px;
  border: 1px solid #ffffff;
  height: auto;
  background-color: white;
  display: flex;
  flex-direction: column;
  padding: 20px;
  z-index: 999999;
}

.crossContainer {
  height: 30px;
  width: 30px;
  border-radius: 8px;
  border: 1px solid #dce5ef;
  display: flex;
  align-items: center;
  justify-content: center;
  &:hover {
    cursor: pointer;
  }
  align-self: flex-end;
}

.crossIcon {
  height: 20px;
  width: 20px;
  path {
    stroke: #001b4c;
  }
}

.title {
  text-align: center;
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  color: #0d2a52;
}

.btn {
  height: 48px;
  width: 128px;
  border-radius: 6px;
  background-color: #ea4a33;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  color: white !important;
}

.cancelBtn {
  height: 48px;
  width: 107px;
  border-radius: 6px;
  border: 2px solid #6c5878;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  color: #0d2a52 !important;
}
