.inputContainer {
  background-color: white !important;
  border: 1px solid #cfdbd5 !important;
  height: 46px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.dateInputToggle {
  display: inline-block;
  position: relative;
  width: 24px;
  height: 24px;
}

.dateInputToggleButton {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url("../../../assets/icons/calender2.svg");
  background-repeat: no-repeat !important;
}

.dateInputStyle {
  border: 0px !important;
  background-color: white !important;
  border: 0px;
  outline: 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  box-sizing: border-box;
  color: black;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}
.dateInputStyle::-webkit-calendar-picker-indicator {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

.dateInputTextStyle {
  border-left: 1px solid rgba(184, 181, 195, 0.2);
  height: fit-content;
  margin-left: 2px;
  padding-left: 5px;
  color: black;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  border: 0px !important;
  &:focus {
    border: 0px !important;
  }
}
