@import "shared/utils/colors.scss";
.main__sidebar {
  z-index: 1;
}
.sidebar__box {
  width: 240px;
  height: 100vh;
  background-color: $WHITE;
  box-shadow: 1px 0px 0px rgb(0 0 0 / 10%);
}
.sidebar__logo {
  margin-bottom: 60px;
  padding-top: 25px;
  display: flex;
  justify-content: center;
}
.sidebar__logo a svg,
.sidebar__logo a img {
  width: 100%;
}
.sidebar__menu-item a {
  display: flex;
  align-items: center;
  padding: 13px 15px 13px 30px;
  color: $TRYIAN_BLUE;
  transition: 0.5s background-color;
  position: relative;
  z-index: 1;
  overflow: hidden;
  &:hover {
    background-color: #fff3ef;
    border-right: 3px solid #ffc4b4;
  }
}

.item-active {
  background-color: #fff3ef;
  border-right: 3px solid #ffc4b4;
  width: 100%;
  height: 100%;
  transition: 0.5s background-color;
}

.sidebar__menu-item a svg {
  width: 20px;
  margin-right: 15px;
}
.sidebar__logo a img {
  display: none;
}
@media only screen and (max-width: 1024px) {
  .main__body {
    width: calc(100% - 80px);
  }
  .sidebar__box {
    width: 80px;
  }

  .sidebar__menu-item a span {
    display: none;
  }

  .sidebar__menu-item a svg {
    margin: 0;
  }
}
@media only screen and (max-width: 992px) {
  .sidebar__logo a svg,
  .sidebar__logo a img {
    padding: 0 5px;
  }
}
@media only screen and (max-width: 767px) {
  .sidebar__logo a svg {
    display: none;
  }
  .sidebar__logo a img {
    display: block;
  }
  .sidebar__box {
    width: 50px;
  }
  .sidebar__menu-item a {
    padding: 13px 15px 13px 14px;
  }
}
