.authSlider {
  /* max-width: 590px; */
  margin: auto;
  overflow: hidden;
}

.authSlideItem {
  padding: 0 50px;
}
.authSlideItem img {
  width: 100%;
  max-height: 630px;
  max-width: 600px;
  width: 100%;
  display: block;
  margin: 0 auto;
  object-fit: cover;
  border-radius: 6px;
}
.authSlideItem p {
  padding-bottom: 30px;
  padding-top: 30px;
  font-size: 20px;
  line-height: 30px;
  color: #495057;
  text-align: center;
  max-width: 400px;
  margin: auto;
  font-weight: 600;
}

@media only screen and (max-width: 1280px) {
  .authSlideItem {
    padding: 0 20px;
  }
}
@media only screen and (max-width: 550px) {
  .authSlideItem img {
    max-height: 360px;
  }
  .authSlideItem p {
    font-size: 18px;
  }
  .authSlideItem {
    padding: 0 10px;
  }
}
