@import "pages/general/aboutus/style.module.scss";

.template h3 {
  margin: 0 0 22px;
}
.contactCols .text {
  width: 50%;
}
.contactCols {
  display: flex;
}
.contactCols .form {
  margin-left: auto;
  width: 43%;
}
.contactCols .formRow {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.contactCols textarea {
  padding: 10px 16px;
  font: 14px/19px "Poppins", sans-serif;
  resize: none;
  height: 110px;
  border-radius: 8px;
  border: 1px solid rgba(207, 219, 213, 0.7);
}
.contactCols .signupBtn {
  height: 55px;
  font-weight: 500;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
}
.contactCols .formRow button[type="submit"] {
  margin: 0;
}
.contactCols .errorMsg {
  position: static;
}
.formRow input {
  border: 1px solid rgba(207, 219, 213, 0.7);
  border-radius: 8px;
  min-height: 40px;
  padding: 0 16px;
  width: 100%;
}

.formRow button[type="submit"] {
  background: #6c5878;
  color: #fff;
  margin-left: 12px;
}

.formRow button {
  border: 1px solid #d0d5dd;
  border-radius: 7px;
  color: #33475b;
  font-size: 14px;
  font-weight: 400;
  padding: 12px 16px;
  text-transform: uppercase;
}

@media (max-width: 767px) {
  .contactCols {
    flex-direction: column;
  }
  .contactCols .text,
  .contactCols .form {
    width: 100%;
  }
}
