.heroContainer {
  background-image: url(../../../assets/images/terms-hero.png);
  background-size: 100% 100%;
  background-position: top;
  padding-top: 228px;
  background-repeat: no-repeat;
  position: relative;
}

.title {
  color: #000;
  text-align: center;
  font-family: Poppins;
  font-size: 46px;
  font-style: normal;
  font-weight: 600;
  line-height: 60px; /* 130.435% */
  letter-spacing: 0.2px;
  position: relative;
}

.titleContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.textDecoration {
  position: relative;
  z-index: 2;
}
.textDecoration:before {
  content: "";
  position: absolute;
  bottom: 10px;
  width: 80%;
  height: 12px;
  background: #ffc4b4;
  z-index: -1;
  right: 0px;
}
