.testimonialSection.testimonialSectionMobile {
  display: none;
}
.sliderImg {
  position: absolute;
}

.slideOne .sliderImg.topBeforeLeft {
  left: -50px;
  top: -50px;
}

.slideRow .item {
  background: #fff;
  width: 397px;
  padding: 0 0;
}
.slideRow .item2 {
  background: #fff;
  width: 397px;
  padding: 0 0;
}
.slideRow .inner {
  margin: -8px 0 0;
  padding: 27px 26px 22px 66px;
}
.slideRow .item:last-child .inner {
  padding: 27px 66px 22px 26px;
}
.slideRow .inner h4 {
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 14px;
}
.slideRow .inner p {
  margin: 0;
  font-size: 12px;
  line-height: 19px;
}
.slideRow {
  display: flex;
}
.slideRow .item:last-child {
  margin-left: auto;
}
.slideRow .item:last-child .icon {
  left: auto;
  right: 0px;
  top: -50px;
}
.testimonialSection .icon {
  position: absolute;
}
.testimonialSection .icon img {
  width: 105px;
  height: 105px;
}
.slideRow .item:last-child {
  margin-right: 60px;
  margin-top: 40px;
}
.innerHolder {
  padding: 70px 0;
  text-align: center;
}
.innerHolder h3,
.testimonialSectionMobile h3 {
  font-size: 41px;
  font-weight: 500;
  color: #183b56;
}
.slideRow .slideOne.item:first-child .icon:before {
  content: "";
  background: #ffc4b4;
  border-radius: 40px;
  width: 53px;
  height: 53px;
  position: absolute;
  top: 0;
  left: -10px;
  z-index: -1;
}
.slideRow .slideOne.item:last-child .icon:before {
  content: "";
  background: #ffc4b4;
  border-radius: 40px;
  width: 53px;
  height: 53px;
  position: absolute;
  top: 0;
  right: -10px;
  z-index: -1;
}
.slideRow .slideTwo.item:first-child .icon:before {
  content: "";
  background: #ffc4b4;
  border-radius: 40px;
  width: 53px;
  height: 53px;
  position: absolute;
  bottom: 0;
  left: -10px;
  top: unset;
  z-index: -1;
}
.slideRow .slideTwo.item:last-child .icon:before {
  content: "";
  background: #ffc4b4;
  border-radius: 40px;
  width: 53px;
  height: 53px;
  position: absolute;
  top: unset;
  bottom: 0;
  right: -10px;
  z-index: -1;
}
.slideRow.slideRowOne {
  max-width: 1100px;
  margin: auto;
  align-items: flex-start;
}
.slideRow.slideRowTwo {
  max-width: 1200px;
  margin: auto;
  align-items: flex-start;
}
.innerLeftImg {
  position: relative;
  margin-right: 80px;
}

.innerLeftImg img {
  height: 98px;
  width: 98px;
}

.innerLeftImg:before {
  content: "";
  position: absolute;
  left: -10px;
  top: -10px;
  width: 60px;
  height: 60px;
  background: #ffc4b4;
  border-radius: 40px;
  z-index: -1;
}
.innerRightImg {
  position: relative;
  margin-left: 100px;
}
.innerRightImg img {
  height: 61px;
  width: 61px;
}

.innerRightImg:before {
  content: "";
  position: absolute;
  right: -5px;
  bottom: -5px;
  width: 60px;
  height: 60px;
  background: #ffc4b4;
  border-radius: 40px;
  z-index: -1;
}
.mySwiper.desktopSwiper {
  padding: 150px 0 60px;
}
.testimonialSection {
  position: relative;
  background: rgba(255, 243, 239, 0.44);
}
.slideOne {
  padding-left: 70px;
}
.slideItem {
  background-color: #fff;
  box-shadow: 3px 4px 24px rgba(202, 227, 231, 0.2);
  border-radius: 20px;
  max-width: 450px;
  padding: 20px;
  position: relative;
}

@media only screen and (max-width: 1280px) {
  .mySwiper.desktopSwiper {
    padding: 120px 0 60px;
  }
  .slideRow .inner {
    margin: -8px 0 0;
    padding: 20px 20px 20px 40px;
  }
  .slideRow .item:last-child .inner {
    padding: 27px 40px 22px 26px;
  }
  .innerHolder {
    padding: 40px 0 80px;
  }
  .slideRow .item:last-child {
    margin-right: 0;
  }
  .slideOne .sliderImg.topBeforeLeft {
    left: 0px;
  }
  .testimonialSection .icon img {
    width: 80px;
    height: 80px;
  }
}

@media only screen and (max-width: 1024px) {
  .testimonialSection {
    display: none;
  }
  .testimonialSection.testimonialSectionMobile {
    display: block;
  }
  .testimonialSection h3 {
    text-align: center;
    font-size: 34px;
    line-height: 35px;
  }
  .slideRow .item:last-child {
    margin: 20px auto 0;
  }
  .mySwiper.mobileSwiper {
    padding: 60px 0 40px;
  }
  .testimonialSectionMobile {
    padding: 40px 0;
  }
}

@media only screen and (max-width: 767px) {
  .testimonialSection {
    display: none;
  }
  .testimonialSection.testimonialSectionMobile {
    display: block;
  }

  .testimonialSection h3 {
    text-align: center;
  }
  .slideRow .item:last-child {
    margin: 20px auto 0;
  }
  .mySwiper.mobileSwiper {
    padding: 60px 0 40px;
  }
  .testimonialSectionMobile {
    padding: 40px 0;
  }
}

@media only screen and (max-width: 550px) {
  .slideRow .item:last-child .inner {
    padding: 15px;
  }
  .slideRow .inner {
    padding: 20px;
  }
  .slideRow .inner h4 {
    font-size: 18px;
  }
}
