.landingHeader {
  position: sticky;
  top: 0;
  z-index: 5;
  background-image: url(../../../assets/images/landing-heroBanner-background.png);
  background-size: cover;
  background-repeat: no-repeat;
  border-bottom: 1px solid rgba(33, 37, 41, 0.04);
  z-index: 9999;
}
.landingHeaderLogo {
  position: relative;
  z-index: 9999;
}
.landingHeaderItem a {
  padding: 10px 18px;
  display: inline-block;
  margin-top: 20px;
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #323c45;
  position: relative;
}
.landingHeaderItem a::before {
  content: "";
  position: absolute;
  top: -20px;
  width: 100%;
  height: 0px;
  background: #ffc4b4;
  left: 0;
  right: 0;
  transition: 0.2s all linear;
}
.landingHeaderItem.active a,
.landingHeaderItem a:hover {
  font-weight: 700;
  letter-spacing: 0.03em;
}
.landingHeaderItem.active a::before,
.landingHeaderItem a:hover::before {
  height: 5px;
}
.mobileHeaderCta {
  display: none;
}
.headerCta {
  gap: 10px;
}
.humbuger {
  display: none;
}
.ctaBtn {
  border: 1px solid #6c5878;
  box-shadow: 0px 1px 2px rgb(16 24 40 / 5%);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  background: #6c5878;
  color: #f8f9fa;
  padding: 10px 20px;
  transition: 0.2s all linear;
  text-transform: capitalize;
}
.ctaBtn svg {
  margin-left: 10px;
  transition: 0.2s all linear;
}
.ctaBtn:hover {
  background-color: transparent;
  color: #6c5878;
}
.ctaBtn:hover svg path {
  fill: #6c5878;
}

@media only screen and (max-width: 420px) {
  .mobileHeaderCta {
    display: block;
  }
  .headerCta {
    display: none;
  }
  .mobileHeaderCta {
    margin-top: 20px;
  }
}

@media only screen and (max-width: 550px) {
  .headerCta {
    display: none;
  }
  .mobileHeaderCta {
    display: block;
    margin-top: 10px;
    width: 100%;
  }
  .mobileHeaderCta a {
    margin: 10px 0;
    width: 100%;
  }
}

@media only screen and (max-width: 767px) {
  .landingHeader {
    padding: 10px 0;
  }
  .landingHeaderMenu {
    position: fixed;
    padding: 0px 20px;
    top: 0;
    left: -100%;
    width: 60vw;
    height: 100vh;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  .landingHeaderMenu.showMenu {
    left: 0;
  }
  .humbuger.active span:nth-child(2) {
    opacity: 0;
    visibility: hidden;
  }
  .humbuger.active span:last-child {
    width: 100%;
    transform: translate(0px, -6px) rotate(90deg);
  }
  .humbuger.active span:first-child {
    transform: translate(0px, 7px) rotate(0deg);
  }
  .humbuger.active {
    transform: rotate(45deg);
  }
  .landingHeaderMenu ul.landingHeaderItems {
    flex-direction: column;
    align-items: flex-start;
  }
  .landingHeaderMenu ul.landingHeaderItems li a {
    margin: 0px;
    padding: 0px;
  }
  .landingHeaderItem a::before {
    content: none;
  }
  .headerCta {
    margin-right: 50px;
  }
  .humbuger {
    display: block;
    width: 30px;
    position: absolute;
    right: 20px;
  }

  .humbuger span {
    width: 100%;
    height: 2px;
    background-color: black;
    display: block;
    transition: 0.5s linear;
  }
  .humbuger span:nth-child(2) {
    margin: 5px 0;
  }
  .humbuger span:last-child {
    width: 80%;
  }
}

@media only screen and (max-width: 1280px) {
  .landingHeaderItem a {
    padding: 10px 5px;
  }
}
