.filterContainer {
  padding-top: 15px;
  padding-bottom: 15px;
}

.applyLabel {
  color: #031e4d;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.clearLabel {
  color: #031e4d;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration-line: underline;
  &:hover {
    cursor: pointer;
  }
}

.singleFilter {
  height: 32px;
  width: fit-content;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-radius: 20px;
  border: 1px solid #dde1e4;
  color: #031e4d;
  gap: 5px;
}

.title {
  font-family: Poppins;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

.value {
  font-family: Poppins;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
}

.icon {
  height: 20px;
  width: 20px;
  path {
    stroke: #031e4d;
  }
  &:hover {
    cursor: pointer;
  }
}
