.reverse {
  display: flex;
  flex-direction: row-reverse !important;
}

.container {
  max-width: calc(50% - 50px);
  width: 100%;
}

.rightAlign {
  justify-content: flex-end !important;
}

.leftAlign {
  text-align: left !important;
}

.rightAlign {
  text-align: right !important;
}

.leftBox {
  background-color: #f2f4f7 !important;
  color: #323c45 !important;
  svg {
    path {
      fill: #323c45;
    }
  }
}

.leftPanel::after {
  background-color: #6c5878 !important;
  border-color: #6c5878 !important;
  right: -5px;
  left: -5px !important;
  /* left: inherit; */
}

.pendingMessage::after {
  width: 50px;
  height: 50px;
  background-image: url("../../../../assets/icons/pending.svg") !important;
  background-repeat: no-repeat;
  background-size: cover;
}
